.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #007d7f;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.main-image {
  width: 100%;
  height: 35rem;
  z-index: -1;
  overflow: hidden;
}

.main-image img {
  width: 100%;
  height: auto;
  /* object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem); */
}
