.form {
  text-align: right;
}

.form button {
  font: inherit;
  cursor: pointer;
  background-color: #3781ad;
  border: 1px solid #fff;
  color: white;
  padding: 0.25rem 2rem;
  border-radius: 20px;
  font-weight: normal;
  text-transform: uppercase;
}

.form button:hover,
.form button:active {
  background-color: #498fb8;
  border-color: #498fb8;
}
