.meals {
  max-width: 80%;
  width: 100%;
  margin: 7rem auto 2rem;
  animation: meals-appear 1s ease-out forwards;
}

.meals ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.mealsloading {
  text-align: center;
  color: black;
}
.mealserror {
  text-align: center;
  color: red;
}

@keyframes meals-appear {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
